<!--  -->
<template>
  <div class="box" @click="jump">
      <div class="boxson" v-if="!isTrue">
          <slot name="origin">1</slot>
      </div>
      <div class="boxson" v-else>
          <slot name="active">2</slot>
      </div>
      <div class="boxson" :class="{ fontColor:isTrue}">
          <slot name="content">3</slot>
      </div>
  </div>
</template>

<script>
export default {
 name:'',
  data () {
    return {

    };
  },
  props:{
      path:{
          type:String,
      }
  },
  methods: {
      jump(){
          this.$router.push(this.path)
      }
  },
  computed:{
      isTrue(){
          return this.$route.path.indexOf(this.path) !=-1
      }
  }
}
</script>
<style scoped>
.box{
    flex: 1;
    color: #a0a0a0;
}
.boxson{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.boxson img{
    width: 27px;
    height: 27px;
}
.fontColor{
    color:#f150a1;
}
</style>