import Vue from 'vue'
import App from './App'
import router from "./router/"
import store from "./store/index.js"

Vue.config.productionTip = false
Vue.prototype.$bus = new Vue()    // 建立一个事件总线
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,     //挂载路由实例
  store,      //挂载vuex
  render:h=>{
    return h(App)
  }
})
