import VueRouter from "vue-router"
import Vue from "vue"

const main = () => import("views/main/MainDetail")
const list = () => import("views/list/ListDetail")
const shoppingcar = () => import("views/shoppingcar/ShoppingCarDetail")
const more = () => import("views/more/MoreDetail")
const detail = ()=>import("views/detail/GoodDetail")
//1.安装路由插件
Vue.use(VueRouter)

//设置其push函数使其在点击同个路由时不发生错误
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => console.log(err))
  }
//2.配置路由详细信息
const routes = [

    {
        path:"",
        redirect: "/main"
    },
    {
        path:"/main",
        component:main

    },
    {
        path:"/list",
        component:list
    },
    {
        path:"/shoppingcar",
        component:shoppingcar
    },
    {
      path:"/more",
      component:more
    },
    {
      path:"/detail/:id",
      component:detail
    }
]
//生成路由实例
const router = new VueRouter({
    routes,
    mode:"history",   //默认情况下是hash模
    // linkActiveClass:"active"
})
//导出路由实例
export default router
