import {ADD_COUNT,PUSH_SHOPPING_CAR} from "./mutation_types"
export default{
    addcar(context,payload){
      const product = context.state.shoppingcar.find(item=>item&&item.iid ==payload.iid)    // 若存在iid相等，则返回对象，没有则为空
      if(product){
        context.commit(ADD_COUNT,product)
      }else{
        context.commit(PUSH_SHOPPING_CAR,payload)
      }
    }
  }